.navbar {
  left: 0;
  right: 0;
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.25rem 1.25rem;
  max-width: 31.625rem;
  margin: 0 auto;
}
@media screen and (min-width: 64rem) {
  .navbar {
    max-width: 75rem;
    padding: 2.5rem 3.125rem;
  }
}
.navbar__logo {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.navbar__logo img {
  margin-right: 5px;
  width: 1.25rem;
  padding: 0.625rem 0;
}
.navbar__logo h1 {
  align-items: center;
  font-size: 1.125rem;
  font-weight: 500;
}
@media screen and (min-width: 64rem) {
  .navbar__logo h1 {
    font-size: 1.5rem;
  }
}
.navbar__button {
  display: none;
  gap: 0.875rem;
}
@media screen and (min-width: 64rem) {
  .navbar__button {
    display: flex;
  }
}